.button {
  background-color: blue;
  padding: 20px 40px;
  border-style: none;
  border-radius: 10px;
  color: honeydew;
  font-weight: 700;
  font-size: 1rem;
  flex-shrink: 0;
  text-decoration: none;
}

.button:hover {
  cursor: pointer;
  opacity: 0.9;
}

.buttonContainer {
  display: flex;
  justify-content: center;
  margin-top: 40px;
}

.text {
  text-align: center;
  margin-top: 90px;
  font-size: 18px;
}

.header {
  text-align: center;
  margin-bottom: 50px;
}